(function () {
    'use strict';

    let hamburger = document.querySelector('.hamburger');
    let menu = document.querySelector('#menu');
    let categoriesTitle = document.querySelector('#categoriesTitle');
    let categories = document.querySelector('#categories');

    hamburger.addEventListener('click', function () {
        hamburger.classList.toggle('is-active');
        if (hamburger.classList.contains('is-active')) {
            console.log('is-active');
            menu.classList.remove('top');
            menu.classList.add('top-0');
        } else {
            menu.classList.remove('top-0');
            menu.classList.add('top');
        }
    })

    categoriesTitle.addEventListener('click', function () {
        if (categories.classList.contains('dnone')) {
            categories.classList.remove('dnone');
            categories.classList.add('dflex');

        } else {
            categories.classList.add('dnone');
            categories.classList.remove('dflex');

        }
    })

})()